.resume-wrapper {
  max-width: 900px;
  margin: 50px auto;
  background: #ffffff;
  box-shadow: 0 0 15px rgba(0,0,0,0.1);
}

.left-panel {
  background: #1B2A33;
  min-height: 100%;
}

.photo-placeholder {
  width: 190px;
  height: 235px;
  background: #ccc;
}

.name {
  font-size: 26px;
  font-weight: 700;
}

.position {
  font-size: 14px;
  letter-spacing: 1px;
}

.section-title {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 10px;
}

.section-text {
  font-size: 13px;
  line-height: 1.4;
}

.skills-list {
  list-style: none;
  padding-left: 0;
  margin: 0;


  columns: 2;
  column-gap: 40px;

  li {
    break-inside: avoid;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    margin-bottom: 8px;
    white-space: nowrap;
  }


  .rating {
    color: #00bfd8;
    font-weight: 600;
    letter-spacing: 0.5px;
  }
}



.right-title {
  font-size: 18px;
  font-weight: 700;
  border-left: 5px solid #00bfd8;
  padding-left: 10px;
}

.contact-list {
  list-style: none;
  padding-left: 0;

  li {
    margin-bottom: 7px;
  }
}
.name, .position {
  transform: rotate(-90deg);
  transform-origin: left top;
  display: inline-block;
}
.text-block {
  margin-right: 120px;
  margin-top: -50px;
}
.photo-placeholder{
  margin-left: 230px;
}

.timeline-section {
  position: relative;
  padding-left: 40px;

  &::before {
    content: "";
    position: absolute;
    left: 15px;
    top: 0;
    bottom: 0;
    width: 2px;
    background: #00bfd8;
  }

  .timeline-item {
    position: relative;
    margin-bottom: 25px;

    &::before {
      content: "";
      position: absolute;
      left: -14px;
      width: 12px;
      height: 12px;
      background: #00bfd8;
      border: 2px solid #00bfd8;
      border-radius: 50%;
    }
  }
}


.timeline-education .timeline-item::before { top: 8px; }
.timeline-contact .timeline-item::before { left: -30px; top: 4px;}
.timeline-job .timeline-item::before { top: 10px; left: -30px;}
.timeline-references .timeline-item::before { top: 6px; left: -30px; }

